html{scroll-behavior:smooth}body,html{height:100%}body{display:flex;flex-direction:column;margin:0;overflow-x:hidden;padding:0;width:100%}*,:after,:before{box-sizing:border-box}body{background-color:#f4f4f4;color:#333;font-family:Helvetica Neue,Helvetica,Arial,sans-serif;font-size:1rem;font-weight:500}main.container{flex:1;margin:0;max-width:100%;padding:0}h5,h6{font-weight:700;margin:1rem 0 0 1.5rem}h1{font-size:1.6rem;margin:1rem 0 0}h1,h2{color:#d27430;font-weight:500}h2{font-size:1.3rem;margin:2rem 0 0 1.5rem}h3{margin:.5rem 0 0 .2rem}h3,h4{font-size:1.3rem;font-weight:500}h4{color:#d27430;margin:1rem 0 0 .2rem}p.date{margin-left:1rem}a{color:#0070f3;-webkit-text-decoration:none;text-decoration:none}a:hover{background-color:rgba(0,112,243,.2);color:#fff}a:active{background-color:#fff;color:#0070f3}footer{background-color:#333;color:#fff;padding:10px 0;text-align:center;width:100%}